.home {
    margin: 20px;
}

.home .image-error {
    -ms-flex-align: center;
        align-items: center;
    background-color: #fafafa;
    display: -ms-flexbox;
    display: flex;
    font-size: 0.5rem;
    height: 160px;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    text-transform: uppercase;
    width: 120px;
}

.home .product-list {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.home .beer-finder-link {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}



